import React from 'react';
import {withStyles} from '@material-ui/core/styles'
import PropTypes from 'prop-types';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import {Grid,Paper} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import classnames from 'classnames';
import Skeleton from 'react-loading-skeleton';
import nonVegImage from '../../images/nonveg.png';
import vegImage from '../../images/veg.png';

const styles = (theme)=>({
    root : {
      maxWidth : '100%',
    },
    card : {
        display:'flex',
        maxWidth:'100%',
        padding : '2%'
    },
    cover:{
        width:'32%',
        height:'38%'
    },
    details:{
        display:'flex',
        flexDirection:'column',
    },
    cardContainerGridItem : {
       display : 'flex',
        // paddingLeft : '2%',
        // paddingRight : '2%'
        maxWidth : '100%',
        paddingLeft :'2%',
    },
    cardContent : {
      padding : 0,
       paddingBottom : '0px !important',
        maxWidth: '100%'
    },

    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        padding : 0,
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        padding : 0,
        transform: 'rotate(180deg)',
    },
    expandedDiv : {
      marginTop : '3%',
      maxWidth : '100%',
      padding : '2%',
    },
    /****   fake component css **/

    fakeContainerGridItem : {
        display : 'flex',
        maxWidth : '100%',
        paddingLeft :'6%',
    }


    /**        ------           ***/
});

class DishComponent extends React.Component
{
    state={
        expanded:false
    };

    handleExpandClick = ()=>{
        this.setState({expanded:!this.state.expanded});
    };

   render()
   {
       const {classes,
           image,
           name,
           price,
           description,
           cuisine,
           nonVeg,
           veg,
           likedBy} = this.props;
        if(!image && !name && !price && !description &&!cuisine && !likedBy){
            let height = window.innerHeight;
            let width = window.outerWidth;
            return(
                <div className={classes.root}>
                    <Card className={classes.card}>
                        <Skeleton height={0.095*height} width={0.295*width}/>
                        <div className={classes.details}>
                            <CardContent component="div" classes={{root : classes.cardContent}}>
                                <Grid container={true} spacing={0}>
                                    <Grid item={true} xs={12} md={12} className={classes.fakeContainerGridItem} style={{marginBottom:'1%'}}>
                                        <Skeleton width={0.59*width} height={0.028125*height}/>
                                    </Grid>
                                    <Grid item={true} xs={12} md={12} className={classes.fakeContainerGridItem} style={{marginBottom:'1%'}}>
                                        <Skeleton width={0.59*width} height={0.028125*height}/>
                                    </Grid>
                                    <Grid item={true} xs={12} md={12} className={classes.fakeContainerGridItem}>
                                        <Skeleton width={0.59*width} height={0.028125*height}/>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </div>
                    </Card>
                </div>
            );
        }

       return(
           <div className={classes.root}>
               <Card className={classes.card}>
                   <CardMedia className={classes.cover} image={image} title={name} component="img" onClick={()=>window.open(image,"_newtab")}/>
                   <div className={classes.details}>
                       <CardContent component="div" classes={{root:classes.cardContent}}>
                           <Grid container={true} spacing={0}>
                               <Grid item={true} xs={12} sm={12} className={classes.cardContainerGridItem} style={{marginBottom:'1%'}}>
                                   <Typography inline={true} noWrap={true} variant="caption" style={{
                                       fontSize : '0.83em',
                                       color: 'maroon'
                                   }}>
                                       {name}</Typography>
                               </Grid>
                               <Grid item={true} xs={12} sm={12} className={classes.cardContainerGridItem} style={{marginBottom:'1%'}}>
                                   <img  src={veg ? vegImage : nonVegImage} style={{height : '92%',width : '9%'}}/>
                                   <Typography  style={{
                                       fontSize : '0.75rem',
                                       color : 'darkslategrey',
                                       marginLeft : '2%',
                                       marginTop : '1%'
                                   }}>
                                       {cuisine}
                                   </Typography>
                                   <Typography  variant="caption" style={{
                                       marginLeft : 'auto',
                                       color : 'maroon',
                                       marginTop:'1%'
                                   }}>{price} &#8377;</Typography>
                               </Grid>
                               <Grid item={true} xs={12} sm={12} className={classes.cardContainerGridItem}>
                                   <Typography inline={true} noWrap={true} variant="caption" style={{
                                       color : 'darkgreen',
                                   }}>{likedBy}% visitors liked it</Typography>
                                   <Typography  variant="caption" style={{
                                       marginLeft : 'auto'
                                   }}><IconButton className={classnames(classes.expand, {
                                       [classes.expandOpen]: this.state.expanded,
                                   })}
                                                  onClick={this.handleExpandClick}
                                                  aria-expanded={this.state.expanded}
                                                  aria-label="Show more">
                                       <ExpandMoreIcon style={{fontSize:'75%'}}/>
                                   </IconButton></Typography>
                               </Grid>
                           </Grid>
                       </CardContent>
                   </div>
               </Card>
               <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
                   <Card className={classes.expandedDiv}>
                       <CardContent style={{padding:0}}>
                           <Typography component="p" gutterBottom={true} style={{
                               fontSize : '0.75rem',
                               color : 'black',
                               textAlign : 'justify'
                           }}>
                               {description}
                           </Typography>
                       </CardContent>
                   </Card>
               </Collapse>
           </div>
       )
   }
}

DishComponent.propTypes={
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(DishComponent);
