import React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux'
import {withStyles} from '@material-ui/core/styles';
import {Grid} from '@material-ui/core';
import PropTypes from 'prop-types';
import actions from '../../redux/actions';
import RestaurantCard from '../../components/restaurantCard';
const{updateRestaurantData} = actions;

const styles = theme =>({
  parent:{
     height:'auto',
     width:'100%',
      backgroundColor:'#F0E68C'
    },
});
class SwipeContainer extends React.Component{
    state={
        start:0,
        limit:5
    };
    constructor(props)
    {
        super(props);
        const{start,limit} = this.state;
        this.fetchRestaurants(start,limit);
    }

    createRestaurantCards=(data)=>{
      let children=[];
      data.map((obj,index)=>{
         children.push(
             <Grid item xs={12} md={3} sm={6} key={index} style={{marginTop:'1%',marginBottom:'1%'}}>
                 <RestaurantCard {...obj} history={this.props.history}/>
             </Grid>
         )
      });
      return children;
    };

    fetchRestaurants=(skip,limit)=>{
                fetch(`http://ec2-13-234-225-152.ap-south-1.compute.amazonaws.com/api/pwa/restaurant/?skip=${skip}&limit=${limit}`,{
                method:'get',
                mode:'cors',
                cache:'no-cache',
                    }).then((response)=>{
                    return response.json();
                }).then((data)=>{
                    const{updateRestaurantData} = this.props;
                    updateRestaurantData(data);
                }).catch((error)=>{
                    console.log(error);
                });
    };

    render()
    {
        const {classes,restaurantData} = this.props;
        if(restaurantData===undefined||restaurantData.length===0)
            return(<h1>iam loading and have no data</h1>);
        return(
            <div className={classes.parent}>
                <Grid container={true} spacing={0}>
                    {this.createRestaurantCards(restaurantData)}
                </Grid>
            </div>
        )
    }
}

SwipeContainer.propTypes={
    classes: PropTypes.object.isRequired,
};

function mapStateToProps(state){
    return{
        ...state,
        restaurantData:state.RestaurantReducer.restaurantData
    }
};
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        updateRestaurantData
    },dispatch);
}

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(SwipeContainer));