import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import {Grid,Paper,InputBase,FormGroup,FormControlLabel,IconButton,Checkbox,Button} from "@material-ui/core";
import SearchIcon from '@material-ui/icons/Search';
import DishComponent from "../../../components/dish";
import {bindActionCreators} from "redux";
import {connect} from 'react-redux';
import actions from '../../../redux/actions';
const {fetchDishes,addFakeDishSkeletons} = actions;


const styles=(theme)=>({
    root : {
        maxWidth : '100%',
    },
    dishRows : {
        height : '5%',
        margin : '2%'
    },
    paper : {
        padding: '2px 4px',
        display: 'flex',
        alignItems: 'center',
        maxWidth: '100%',
    },

});
class DishContainer extends React.Component{
    state={
        veg : false,
        nonveg : false,
        limit : 6,
    };

    // constructor(props)
    // {
    //     super(props);
    //     const {fetchDishes,start} = props;
    //     const {limit} = this.state;
    //     fetchDishes(start,limit);
    //     // setTimeout((start,limit)=>fetchDishes(start,limit),5000,start,limit);
    // }

    componentDidMount() {
        const {fetchDishes, start} = this.props;
        const {limit} = this.state;
        fetchDishes(start, limit);
    }


    /**event handlers**/
    handleVegChange=(event,data)=>{
        this.setState({veg:!data});
    };
    handleNonVegChange=(event,data)=>{
        this.setState({nonveg:!data});
    };

    /*----------**/


    createDishes=(data)=>{
        const {classes} = this.props;
        let children=[];
        for(let  i =0;i<data.length;i++)
        {
            children.push(
                <Grid item={true} xs={12} key={i} classes={{item : classes.dishRows}}>
                    <DishComponent {...data[i]}/>
                </Grid>
            )
        }
        return children;
    };

    bringMore=()=> {
        const {fetchDishes, addFakeDishSkeletons ,start} = this.props;
        const {limit} = this.state;
        addFakeDishSkeletons();
        fetchDishes(start,limit);
        // setTimeout((start,limit)=>fetchDishes(start,limit),2000,start,limit);
    };

    render()
    {
        const {classes,dishData} = this.props;
        return(
            <div className={classes.root}>
                <Grid container={true} spacing={0}>
                    <Grid item={true} xs={12} md={12}>
                        <Paper className={classes.paper} elevation={1}>
                            <InputBase className={classes.input} placeholder="Search Dishes"/>
                                <IconButton className={classes.iconButton} aria-label="Search">
                                    <SearchIcon/>
                                </IconButton>
                        </Paper>
                    </Grid>
                    <Grid item={true} xs={12} sm={12}>
                        <FormGroup row>
                            <FormControlLabel control={<Checkbox checked={this.state.veg} value={this.state.veg.toString()} onChange={(e)=>this.handleVegChange(e,this.state.veg)}/>} label="Veg Only"/>
                            <FormControlLabel control={<Checkbox checked={this.state.nonveg} value={this.state.nonveg.toString()} onChange={(e)=>this.handleNonVegChange(e,this.state.nonveg)}/>} label="Non-Veg Only"/>
                        </FormGroup>
                    </Grid>
                </Grid>
                <Grid container={true} spacing={0}>
                    {this.createDishes(dishData)}
                </Grid>
                <Button onClick={()=>this.bringMore()}>
                    Load More
                </Button>
            </div>
        )
    }
};

DishContainer.propTypes={
    classes : PropTypes.object.isRequired,
};

function mapStateToProps(state) {
    return {
        ...state,
        dishData : state.DishReducer.dishData,
        start : state.DishReducer.start,
    }
}
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        fetchDishes,addFakeDishSkeletons
    },dispatch);
}

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(DishContainer));

