import {takeLatest , call, put , all, fork,takeEvery} from 'redux-saga/effects';
import axios from 'axios';
import actions from '../actions';
import Data from '../../DummyData/review';

export function* fetchReviewsSaga() {
    console.log("this saga is running");
    yield takeLatest(actions.FETCH_REVIEWS,function*(actionObj) {
        console.log("Success object dispatched");
        yield put({
            type : actions.FETCH_REVIEWS_SUCCESS,
            payload : Data
        });
    });
}

export default function* rootSaga() {
    yield all(
        [
            fork(fetchReviewsSaga),
        ]
    )
}