import {takeLatest,takeEvery , call, put , all, fork} from 'redux-saga/effects';
import axios from 'axios';
import actions from '../actions';
import Data from '../../DummyData/dish';

export function* fetchDishesSaga() {
    yield takeEvery(actions.FETCH_DISHES,function* (actionObj) {
        yield put({
            type : actions.FETCH_DISHES_SUCCESS,
            payload : Data,
        })
    });
}


export default function* rootSaga() {
    yield all(
        [
            fork(fetchDishesSaga),
        ]
    )
}