import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import {Grid , Button} from '@material-ui/core';
import ReviewComponent from "../../../components/review";
import {connect} from 'react-redux';
import actions from '../../../redux/actions';
import {bindActionCreators} from "redux";

const {fetchReviews,addFakeReviewSkeletons} = actions;
const styles=(theme)=>({
    root : {
        maxWidth : '100%'
    },
    reviewRows: {
        height : '7%',
        margin : '2%'
    }
});

class ReviewContainer extends React.Component{

    state = {
        limit : 6,
    };

    componentDidMount()
    {
        const {fetchReviews,start} = this.props;
        const {limit} = this.state;
        fetchReviews(start,limit);
        // setTimeout((start,limit)=>fetchReviews(start,limit),5000,start,limit);
    }


    createReviews=(Data)=>{
        const {classes} = this.props;
        let children = [];

        for(let i=0;i<Data.length;i++)
        {
            children.push(
                <Grid item={true} xs={12} key={i} classes={{item :classes.reviewRows }}>
                    <ReviewComponent {...Data[i]}/>
                </Grid>
            )
        }

        return children;
    };

    loadMore=()=>{
      const {addFakeReviewSkeletons,fetchReviews,start} = this.props;
      const {limit} = this.state;
      addFakeReviewSkeletons();
        fetchReviews(start,limit);
      // setTimeout((start,limit)=>fetchReviews(start,limit),2000,start,limit);
    };


    render()
    {
        const {classes} = this.props;
        const {reviewData} = this.props;
        return(
            <div className={classes.root}>
                <Grid container={true} spacing={0}>
                    {console.log("in props = ",reviewData)}
                    {this.createReviews(reviewData)}
                </Grid>
                <Button onClick={()=>this.loadMore()}>
                    Load More
                </Button>
            </div>
        )
    }
};

ReviewContainer.propTypes={
    classes : PropTypes.object.isRequired,
};
function mapStateToProps(state){
    return{
        ...state,
        reviewData:state.ReviewReducer.reviewData,
        start : state.ReviewReducer.counter,
    }
}
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        fetchReviews,addFakeReviewSkeletons
    },dispatch);
}

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(ReviewContainer));