import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import PropTypes from 'prop-types';
import {Grid, IconButton, TextField, Button, Snackbar, CircularProgress} from '@material-ui/core';
import {Link} from "react-router-dom";
import {ArrowBack, RemoveRedEyeTwoTone} from "@material-ui/icons";
import axios from 'axios';

import docCookies from '../../cookie';
import MySnackbarContentWrapper from "../../components/snackbars";

const styles={
    parent:{
        width:'100vw',
        height:'100vh',
        position:'relative'
    },
    unloadingChild:{
        width:'100%',
        height:'100%'
    },
    loadingChild:{
        width:'100%',
        height:'100%',
        backgroundColor:'#fff0f5',
        opacity:'0.6',
        zIndex:100,
        position:'fixed'
    },
    progress:{
        marginTop:'50%',
        marginLeft:'40%',
        opacity:'1',
    },
    signupButton:{
      width:'50%',
      fontStyle:'fantasy',
      fontWeight:'bold',
      backgroundColor:'#006400',
      color:'white'
    },
};

class SIGNUP extends React.Component{
    state={
        passVisible:false,
        loading:false,
        snackbarOpen:false,
        messageType:''
    };

    signUp = ()=>{
        if(this.checkInfo())
        {
           this.setState({loading:true});
            const{name,phone,email,password} = this.state;
           axios({
               method:'post',
               url:'http://ec2-13-234-225-152.ap-south-1.compute.amazonaws.com/api/registerV2',
               data:{
                   name,phone,email,password
               },
               headers:{
                   'Content-Type':'application/json'
               }
           }).then((response)=>{
              docCookies.setItem('token',response.data,86400*7);
              docCookies.setItem('name',this.state.name,86400*7);
              docCookies.setItem('email',this.state.email,86400*7);
              docCookies.setItem('phone',this.state.phone,86400*7);
               this.setState({loading:false,snackbarOpen:true,messageType:'success'});
               setTimeout(()=>{this.props.history.push('/')},2000);

           }).catch((err)=>{
               console.log(err);
               this.setState({loading:false,snackbarOpen:true,messageType:'error'});
           });
        }
    };

    checkInfo=()=>{
      const{name,phone,email,password} = this.state;
      return !!(name && phone && email && password);
    };

    handleClose = () => {
        this.setState({ snackbarOpen: false });
    };

    nameChange=(event)=>{
        this.setState({name:event.target.value});
    };
    phoneChange=(event)=>{
        this.setState({phone:event.target.value});
    };
    passChange=(event)=>{
        this.setState({password:event.target.value});
    };
    emailChange=(event)=>{
        this.setState({email:event.target.value});
    };

    toggleVisibility = ()=>{
        let x = this.state.passVisible;
        this.setState({passVisible:!x});
    };

    render()
    {
        const {classes} = this.props;
        const {snackbarOpen,messageType} = this.state;
        return(
            <div className={classes.parent}>
                {
                    (this.state.loading)&&(
                        <div className={classes.loadingChild}>
                            <CircularProgress  color="secondary" size={60} className={classes.progress}/>
                        </div>
                    )
                }
                <div className={classes.unloadingChild}>
                <Grid container={true} spacing={0} style={{paddingLeft:'16px',paddingRight:'16px'}}>
                    <Grid item xs={12} style={{marginTop:'7%'}}>
                        <Link to="/"><IconButton style={{paddingLeft:'0px'}}>
                            <ArrowBack/>
                        </IconButton></Link>
                    </Grid>
                </Grid>
                <Grid container={true} spacing={0} style={{paddingLeft:'32px',paddingRight:'32px'}}>
                    <Grid item xs={12} style={{marginTop:'14%'}}>
                        <TextField
                            id="name"
                            label="Name"
                            style={{
                                width:'100%'
                            }}
                            name="name"
                            value={this.state.name || ''}
                            onChange={this.nameChange}
                        />
                    </Grid>
                    <Grid item xs={12} style={{marginTop:'10%'}}>
                        <TextField
                            id="emailID"
                            label="Email ID"
                            style={{
                                width:'100%'
                            }}
                            name="email"
                            value={this.state.email || ''}
                            onChange={this.emailChange}
                        />
                    </Grid>
                    <Grid item xs={12} style={{marginTop:'10%'}}>
                        <TextField
                            id="phone"
                            label="Mobile Number"
                            style={{
                                width:'100%'
                            }}
                            name="phone"
                            value={this.state.phone || ''}
                            onChange={this.phoneChange}
                            type="number"
                        />
                    </Grid>
                    <Grid item xs={12} style={{marginTop:'10%'}}>
                        <TextField
                            type={(this.state.passVisible)?"text":"password"}
                            id="password"
                            label="Password"
                            style={{
                                width:'90%'
                            }}
                            name="password"
                            value={this.state.password || ''}
                            onChange={this.passChange}
                        />
                        <IconButton style={{width:'10%'}} onClick={()=>this.toggleVisibility()}><RemoveRedEyeTwoTone/></IconButton>
                    </Grid>
                    <Grid item xs={12} style={{marginTop:'20%',textAlign:'center'}}>
                        <Button className={classes.signupButton} onClick={()=>this.signUp()}>Sign Up</Button>
                    </Grid>
                </Grid>
                    <Snackbar open={snackbarOpen} autoHideDuration={5000} onClose={this.handleClose}>
                        <MySnackbarContentWrapper
                            onClose={this.handleClose}
                            variant={messageType==='success'? "success" : "error"}
                            message={messageType==='success' ? "You are successfully signed in" : "There is some error in signing up"}
                        />
                    </Snackbar>
                </div>
            </div>
        )
    }
}

SIGNUP.propTypes={
    classes: PropTypes.object.isRequired,
};

function mapStateToProps(state)
{
    return{
        ...state
    }
}
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({},dispatch);
}

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(SIGNUP));