import React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import {Typography} from '@material-ui/core';
import {CardGiftcard,LocalPhone,KeyboardArrowRight} from '@material-ui/icons';
import actions from '../../redux/actions/index';
import {Link} from 'react-router-dom';
import ChefIcon from '../../components/icons/chefIcon';

const {toggleSidebar} = actions;
const styles={
    list:{
        width:250
    }
};


class Sidebar extends React.Component{
    state={

    };

    componentWillReceiveProps(nextProps)
    {
    }

    toggleDrawer = (val) =>{
        this.props.toggleSidebar(val);
    };

    render()
    {
        const {classes} = this.props;
        const sideList = (
            <div className={classes.list}>
                <List>
                    <ListItem>
                     <Grid container={true} spacing={0} justify="center">
                         <Grid item xs={12} style={{textAlign:'center'}}>
                             <Typography style={{
                                 fontSize:'0.8em'
                             }}>
                                 Find out sizzling food near you</Typography>
                             <Typography variant="subtitle1" gutterBottom={true}><b>anytime anywhere</b></Typography>
                         </Grid>
                         <Grid item xs={12} style={{textAlign:'center'}}>
                            <ChefIcon/>
                         </Grid>
                     </Grid>
                    </ListItem>

                    <ListItem style={{
                        marginBottom:'7%'
                    }}>
                    <Button style={{
                        width:'100%',
                        backgroundColor:'green'
                    }}>
                        <Link to="/login" style={{
                            textDecoration:'none',
                            color:'white',
                        }}>LOGIN</Link>
                    </Button>
                    </ListItem>

                    <Divider variant="fullWidth"/>

                    <ListItem>
                        <ListItemIcon>
                            <CardGiftcard/>
                        </ListItemIcon>
                        <ListItemText primary="FV points"/>
                        <IconButton style={{
                            marginRight:-18
                        }}><KeyboardArrowRight/></IconButton>
                    </ListItem>

                    <Divider variant="fullWidth"/>

                    <ListItem>
                        <ListItemIcon>
                            <LocalPhone/>
                        </ListItemIcon>
                        <ListItemText primary="Contact Us"/>
                        <a href="tel:+918447271736"><IconButton style={{
                            marginRight:-18
                        }}><KeyboardArrowRight/></IconButton></a>
                    </ListItem>

                </List>
            </div>
        );

         return(
           <div>
               <SwipeableDrawer anchor="right" onClose={()=>{this.toggleDrawer(false)}}
                                onOpen={()=>{this.toggleDrawer(true)}} open={this.props.drawerOpen}
                               disableSwipeToOpen={true}>
                   <div
                       tabIndex={0}
                       role="button"
                       onClick={()=>this.toggleDrawer(false)}
                       onKeyDown={()=>this.toggleDrawer(false)}
                   >
                       {sideList}
                   </div>
               </SwipeableDrawer>
           </div>
         );
    }
}
Sidebar.propTypes = {
  classes : PropTypes.object.isRequired
};

const mapStateToProps = (state) => {
    return {
        ...state,
        //reducer variables
        drawerOpen:state.layoutReducer.openSidebar
    }
};

const mapDispatchToProps = (dispatch) =>{
    return bindActionCreators({
        toggleSidebar,
    },dispatch)
};


export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(Sidebar));

