import React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Grid,IconButton,Typography,InputBase} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import {LocalPhone} from '@material-ui/icons'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import logo from '../../images/logo2.png';
import actions from '../../redux/actions/index';
import Fab from "@material-ui/core/Fab";
import Footer from '../footer';
import InfoComponent from '../../components/info';
import SwipeContainer from '../../containers/swipeContainer';
import docCookies from '../../cookie/index';

const {toggleSidebar} = actions;

const styles = theme => ({
    parent:{
      height:"100vh",
        width:"100vw"
    },
    child1: {
        flexGrow: 1,
        height:'70%',
        // paddingBottom:'30%',
        backgroundColor:'#f2f2f2',
        // backgroundImage:`url(${logo})`,
        // backgroundRepeat:'no-repeat',
        // backgroundSize:'cover'
    },
    child2:{
      height:'30%',
        width:'100%',
    },
    // child3:{
    //   // height:'100%',
    //   backgroundColor:'#F0E68C',
    //     height:'auto',
    //     width:'100%'
    //     // overflowY:'auto'
    // },
    menuButton: {
        marginRight: 0,
        marginTop:2,
    },
    rowMargin:{
      marginTop:"100px",
        backgroundColor:"#f8f8f8"

    },
    phoneButton:{
      marginTop:2,
    },
    searchPaper:{
        display:'flex',
        height:'50px',
        width:'100%',
    },
    searchInput:{
        width:'100%',
        textAlign:'center !important',
        paddingLeft:'10px'
    },
    gridStatic:{
        marginTop:'20%',paddingLeft:'15px',paddingRight:'15px',textAlign:'center'
    },
    gridDynamic:{
        paddingLeft:'15px',paddingRight:'15px',textAlign:'center',position:'fixed',top:'2%',width:'100%',zIndex:105
    },
    fabMenu:{

    }
});

class MainPage extends React.Component{
    state={
        offsetHeightSearch:0,
        passedViewportForSearch:false,
        popUpMenu:false,
    };

    relaxation = Math.ceil(0.007*window.innerHeight);

    componentDidMount()
    {
        let val = document.getElementById("searchPaperGrid").offsetTop;
        this.setState({offsetHeightSearch:val});
        window.addEventListener('scroll',this.listenScrollEvent);
        // window.scrollbars.visible=false;
    }

    componentWillUnmount()
    {
        window.removeEventListener('scroll',this.listenScrollEvent);
    }

    sidebarOpenDrawerByButton = (val)=>{
        this.props.toggleSidebar(!val);
    };

    listenScrollEvent = (event)=>{
        if(this.state.offsetHeightSearch<window.scrollY-this.relaxation)
        {
            this.setState({passedViewportForSearch:true,popUpMenu:true});
        }
        else
        {
            this.setState({passedViewportForSearch:false,popUpMenu:false});
        }
    };

    render()
    {
        const {classes} = this.props;
        return(
            <div className={classes.parent}>
                <div className={classes.child1}>
                    <Grid container={true} spacing={0}>
                        {
                            /**
                             * layer 1
                             * menuButton
                             * empty space
                             * phone button
                             */
                        }

                        <Grid item xs={8} style={{textAlign:'left'}}>
                            <img src={logo} style={{height:"inherit",width:'60%',marginLeft:"15px"}} alt=""/>
                        </Grid>
                        <Grid item xs={2}>
                            <IconButton className={classes.phoneButton}>
                                <LocalPhone style={{fontSize:"35px",marginTop:'10px'}} alt=""/>
                            </IconButton>
                        </Grid>
                        <Grid item xs={2}>
                            <IconButton className={classes.menuButton} onClick={()=>this.sidebarOpenDrawerByButton(this.props.openSidebar)}>
                                <MenuIcon style={{fontSize:"35px",marginTop:'10px'}}/>
                            </IconButton>
                        </Grid>
                        {
                            /**
                             * Layer 2
                             *
                             * empty space
                             * Name
                             * empty space
                             */
                        }
                        <Grid item xs={1} className={classes.rowMargin}/>
                        <Grid item xs={10} className={classes.rowMargin}>
                            <Typography align='center' gutterBottom={true} variant='h6'
                            style={{
                                fontSize:'1.3em',
                                color:'#0d5f7c',
                                fontWeight:'bold',
                                fontFamily:'fantasy'
                            }}
                            >Feel the magic of reviews</Typography>
                        </Grid>
                        <Grid item xs={1} className={classes.rowMargin}/>
                        {
                            /**
                             * Layer 4
                             *
                             * empty space
                             * input element
                             * empty space
                             */
                        }
                        <Grid item xs={12} className={
                            (this.state.passedViewportForSearch)?classes.gridDynamic:classes.gridStatic
                        } id="searchPaperGrid">
                            <Paper className={
                                classes.searchPaper
                            }>
                                <InputBase className={classes.searchInput} placeholder="Search for Restaurants" />
                                    <SearchIcon style={{color:'green',fontWeight:'bolder',fontSize:'36px',paddingTop:'1%',
                                    paddingRight:'1%'}} />
                            </Paper>
                        </Grid>
                    </Grid>
                </div>
                <div className={classes.child2}>
                    {/*<InfoComponent/>*/}
                </div>
                    <SwipeContainer history={this.props.history}/>
                {
                    (this.state.popUpMenu) && (!docCookies.getItem('token')) && (
                        <Fab aria-label="Menu" style={{
                            backgroundColor: '#663399',
                            position: 'fixed',
                            right: '5%',
                            bottom: '5%'

                        }} onClick={() => this.sidebarOpenDrawerByButton(this.props.openSidebar)}>
                            <MenuIcon style={{fill: 'white'}}/>
                        </Fab>
                    )
                }
                {

                    (this.state.popUpMenu)&&(docCookies.getItem('token'))&&(
                        <Footer/>
                    )

                }
            </div>
        )
    }
}

MainPage.propTypes = {
    classes: PropTypes.object.isRequired,
};

function mapStateToProps(state)
{
    return {
        ...state,
        openSidebar : state.layoutReducer.openSidebar,
    }
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        toggleSidebar
    },dispatch)
}

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(MainPage));