import actions from '../actions/index';

const initState = {openSidebar:false,reviewData : [{},{},{},{},{},{}],start : 0};
export default function restaurantReducer(state=initState,action){
    let {reviewData} = state;
    switch (action.type) {
        case actions.FETCH_REVIEWS_SUCCESS :
            console.log("this fired");
            console.log("reviewData = ",reviewData);
            if(checkreviewDataIsAtInitialState(reviewData))
            {
                console.log("yahan");
                return {
                    ...state,
                    reviewData : action.payload,
                    start : action.payload.length
                }
            }
            else
            {
                console.log("yahan aayi");
                reviewData.splice(reviewData.length-2,2);
                let ans = reviewData.concat(action.payload);
                return {
                    ...state,
                    reviewData : ans,
                    start : ans.length
                }
            }
        case actions.FETCH_REVIEWS_FAILURE :
            return {
                ...state
            };
        case actions.ADD_FAKE_REVIEW_SKELETON :
            return {
                ...state,
                reviewData : reviewData.concat([{},{}]),
            };
        default : return {
            ...state
        }
    }
}
function checkreviewDataIsAtInitialState(reviewData) {
    if(reviewData.length !== 6)
        return false;
    for(let i=0;i<reviewData.length;i++)
    {
        if(!Object.entries(reviewData[i]).length === 0 || !reviewData[i].constructor === Object)
            return false;
    }
    return true;
}