import actions from '../actions/index';

const initState = {openSidebar:false,restaurantData:[]};
export  default function layout_reducer(state = initState , action)
{
    switch (action.type) {
        case actions.TOGGLE_SIDEBAR:
            return {
                ...state,
                openSidebar : action.payload
            };
        default :
            return state;
    }
}