import React from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import {withStyles} from '@material-ui/core/styles'
import classNames from 'classnames';
import PropTypes from 'prop-types';
import Fab from '@material-ui/core/Fab';
import {Typography,IconButton,TextField,Button,CircularProgress,Snackbar,Dialog,DialogTitle} from '@material-ui/core';
import {ArrowBack,EmailRounded,RemoveRedEyeTwoTone,Check} from '@material-ui/icons';
import {purple,green} from '@material-ui/core/colors'
import FacebookIcon from '../../components/icons/facebookIcon';
import GoogleIcon from '../../components/icons/googleIcon';
import MySnackbarContentWrapper from '../../components/snackbars';
import axios from 'axios';
import docCookies from "../../cookie";
/** must do % based margin in this container**/



const styles = theme => ({
   parent:{
       width:'100vw',
       height:'100vh',
       position:'relative'
   },
   unloadingChild:{
      width:'100%',
      height:'100%'
   },
    loadingChild:{
      width:'100%',
      height:'100%',
      backgroundColor:'#fff0f5',
      opacity:'0.6',
      zIndex:100,
      position:'fixed'
    },
    progress:{
        marginTop:'50%',
        marginLeft:'40%',
        opacity:'1',
    },
    fab:{
        backgroundColor:'#008000',
        position:'fixed',
        right:'5%',
        bottom:'5%'
    },
    fabMoveUp:{
        transform: 'translate3d(0, -46px, 0)',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.enteringScreen,
            easing: theme.transitions.easing.easeOut,
        }),
    },
    fabMoveDown:{
        transform: 'translate3d(0, 0, 0)',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.leavingScreen,
            easing: theme.transitions.easing.sharp,
        }),
    },
    cssLabel: {
        '&$cssFocused': {
            color: purple[500],
        },
    },
    cssFocused: {},
    cssOutlinedInput: {
        '&$cssFocused $notchedOutline': {
            borderColor: purple[500],
        },
    },
    notchedOutline: {},
});

const messages = {
  'success':'You are successfully signed in',
  'error':'There is some error in logging you in',
  'warning':'Please fill out fields'
};
class LoginPage extends React.Component{
  state={
      color:green,
      emailPressed:false,
      passVisible:false,
      loading:false,
      snackbarOpen:false,
      messageType:'',
      requestOTP:false,
      otp:'',
  };

  changeEmailState = (val)=>{
      this.setState({emailPressed:val});
  };

  toggleVisibility = ()=>{
    let x = this.state.passVisible;
    this.setState({passVisible:!x});
  };

    handleClose = () => {
        this.setState({ snackbarOpen: false });
    };

    handleEmailChange=(event)=>{
      this.setState({email:event.target.value});
    };

    handlePasswordChange=(event)=>{
        this.setState({password:event.target.value});
    };

    tellServerToSendOTP=()=>{
        axios({
            method:'post',
            url:`http://ec2-13-234-225-152.ap-south-1.compute.amazonaws.com/api/authentication/send-otp`,
            data:{
                phone : this.state.phone
            },
            headers:{
                'Content-Type':'application/json'
            }
        }).then((response)=>console.log(response)).catch((err)=>console.error(err));
    };

    resendOtp=()=>{
      this.tellServerToSendOTP();
    };

    toggleRequestOtp=(val)=>{
        if(this.state.phone){
            this.tellServerToSendOTP();
            this.setState({requestOTP:val});
        }
        else
            this.setState({snackbarOpen:true,messageType:'warning'});
    };

    handleOTPChange=(event)=>{
        this.setState({otp:event.target.value});
    };
    handlePhoneChange=(event)=>{
      this.setState({phone:event.target.value});
    };

    loginViaEmail=()=>{
        const{email,password} = this.state;
        if(email&&password)
        {
            this.setState({loading:true});
            axios({
                method:'post',
                url:'http://ec2-13-234-225-152.ap-south-1.compute.amazonaws.com/api/login',
                data:{
                    email,password
                },
                headers:{
                    'Content-Type':'application/json'
                }
            }).then((response)=>{
                docCookies.setItem('token',response.data.token,86400*7);
                docCookies.setItem('name',response.data.user.name,86400*7);
                docCookies.setItem('phone',response.data.user.phone,86400*7);
                docCookies.setItem('email',this.state.email,86400*7);
                this.setState({loading:false,snackbarOpen:true,messageType:'success'});
                setTimeout(()=>{this.props.history.push('/')},1300);
            }).catch((err)=>{
                this.setState({loading:false,snackbarOpen:true,messageType:'error'});
               console.log(err);
            });
        }
        else
        {
            this.setState({snackbarOpen:true,messageType:'warning'});
        }
    };

    confirmOTP=()=>{
        if(this.state.otp)
        {
            this.setState({loading:true});
            axios({
                method:'post',
                url:'http://ec2-13-234-225-152.ap-south-1.compute.amazonaws.com/api/authentication/confirm-otp',
                data:{
                    otp:this.state.otp
                },
                headers:{
                    'Content-Type':'application/json'
                }
            }).then((response)=>{
                this.setState({snackbarOpen:true,messageType:'success',loading:false});
                setTimeout((history)=>{history.push('/')},1000,this.props.history);
            }).catch((err)=>{
                console.log(err);
                this.setState({snackbarOpen:true,messageType:'error',loading:false})
            });
        }
        else
        {
            this.setState({snackbarOpen:true,messageType:'warning'});
        }
    };

  render(){
      const {classes} = this.props;
      const {snackbarOpen,messageType} = this.state;
      const fabClassName = classNames(classes.fab,snackbarOpen?classes.fabMoveUp:classes.fabMoveDown);

      if(this.state.requestOTP)
      {
          return(
              <div className={classes.parent}>
                  {
                      (this.state.loading)&&(
                          <div className={classes.loadingChild}>
                              <CircularProgress  color="secondary" size={60} className={classes.progress}/>
                          </div>
                      )
                  }
                  <Grid container={true} spacing={0} style={{paddingLeft:'16px',paddingRight:'16px'}}>
                      <Grid item xs={12} style={{marginTop:'7%'}}>
                          <Link to="/"><IconButton style={{paddingLeft:'0px'}}>
                              <ArrowBack/>
                          </IconButton></Link>
                      </Grid>
                      <Grid item xs={12} style={{marginTop:'5%',paddingLeft:'5%',paddingRight:'5%'}}>
                          <TextField type="number"
                                     style={{
                                         width : '100%',
                                     }}
                                     InputLabelProps={{
                                         classes: {
                                             root: classes.cssLabel,
                                             focused: classes.cssFocused,
                                         },
                                     }}
                                     InputProps={{
                                         classes: {
                                             root: classes.cssOutlinedInput,
                                             focused: classes.cssFocused,
                                             notchedOutline: classes.notchedOutline,
                                         },
                                     }}
                                     label="Enter OTP"
                                     variant="outlined"
                                     id="custom-css-outlined-input2"
                                     value={this.state.otp}
                                     onChange={this.handleOTPChange}
                          />
                      </Grid>
                      <Grid item xs={6} style={{marginTop:'4%',paddingLeft:'5%',paddingRight:'5%'}}>
                          <a style={{textDecoration:'none',}}><Typography style={{
                              fontSize:'0.7em',
                              textAlign:'left',
                              color:'#800000'
                          }} onClick={()=>this.resendOtp()}>Resend OTP </Typography></a>
                      </Grid>
                      <Grid item xs={12} style={{marginTop:'7%',textAlign:'center'}}>
                          <Button variant="text" style={{
                              backgroundColor:'green',
                              color:'white',
                              width:'50%'
                          }} onClick={()=>this.confirmOTP()}>
                              Confirm OTP
                          </Button>
                      </Grid>
                  </Grid>
                  <Snackbar open={snackbarOpen} autoHideDuration={3000} onClose={this.handleClose}>
                      <MySnackbarContentWrapper
                          onClose={this.handleClose}
                          variant={messageType}
                          message={messages[messageType]}
                      />
                  </Snackbar>
              </div>
          )
      }


      if(this.state.emailPressed)
      {
          return(
              <div className={classes.parent}>
                  {
                      (this.state.loading)&&(
                          <div className={classes.loadingChild}>
                              <CircularProgress  color="secondary" size={60} className={classes.progress}/>
                          </div>
                      )
                  }
                  <div className={classes.unloadingChild}>
                      <Grid container={true} spacing={0} style={{paddingLeft:'16px',paddingRight:'16px'}}>
                          <Grid item xs={12} style={{marginTop:'7%'}}>
                              <Link to="/"><IconButton style={{paddingLeft:'0px'}}>
                                  <ArrowBack/>
                              </IconButton></Link>
                          </Grid>
                      </Grid>
                      <Grid container={true} spacing={0} style={{paddingLeft:'32px',paddingRight:'32px'}}>
                          <Grid item xs={12} style={{marginTop:'14%'}}>
                              <TextField
                                  label="Email ID"
                                  style={{
                                      width:'100%'
                                  }}
                                  name="email"
                                  value={this.state.email || ''}
                                  onChange={this.handleEmailChange}
                              />
                          </Grid>
                          <Grid item xs={12} style={{marginTop:'10%'}}>
                              <TextField
                                  type={(this.state.passVisible)?"text":"password"}
                                  label="Password"
                                  style={{
                                      width:'90%'
                                  }}
                                  name="password"
                                  value={this.state.password || ''}
                                  onChange={this.handlePasswordChange}
                              />
                              <IconButton style={{width:'10%'}} onClick={()=>this.toggleVisibility()}><RemoveRedEyeTwoTone/></IconButton>
                          </Grid>
                          <Grid item xs={6} style={{marginTop:'7%'}}>
                              <a href="/forgotPassword" style={{textDecoration:'none',}}><Typography style={{
                                  fontSize:'0.7em',
                                  textAlign:'left',
                                  color:'#800000'
                              }}>Forgot your password ?</Typography></a>
                          </Grid>
                      </Grid>
                      <Grid container={true} spacing={0} style={{paddingLeft:'32px',paddingRight:'32px'}}>
                          <Grid item xs={12} sm={8} md={6} style={{marginTop:'20%'}}>
                              <a href="/signup" style={{textDecoration:'none',}}><Typography style={{
                                  fontSize:'0.7em',
                                  textAlign:'center',
                                  color:'#800000'
                              }}>Do not have an Account ? Sign up</Typography></a>
                          </Grid>
                      </Grid>
                      <Fab aria-label="signIn" className={fabClassName}
                      onClick={()=>this.loginViaEmail()}>
                        <Check style={{fill:'white'}}/>
                      </Fab>
                      <Snackbar open={snackbarOpen} autoHideDuration={3000} onClose={this.handleClose}>
                          <MySnackbarContentWrapper
                              onClose={this.handleClose}
                              variant={messageType}
                              message={messages[messageType]}
                          />
                      </Snackbar>
                  </div>
              </div>
          )
      }
      return(<div className={classes.parent}>
        <Grid container={true} spacing={0} style={{paddingLeft:'16px',paddingRight:'16px'}}>
            <Grid item xs={12} style={{marginTop:'7%'}}>
                <Link to="/"><IconButton style={{paddingLeft:'0px'}}>
                    <ArrowBack/>
                </IconButton></Link>
            </Grid>
            <Grid item xs={12} style={{marginTop:'35px'}}>
                <Typography align="left" inline={true} style={{
                    fontSize:'1.1em'
                }}>Continue with your mobile number</Typography>
            </Grid>
            <Grid item xs={12} style={{marginTop:'20px'}}>

                {/***----------------**/}

                <TextField type="number"
                           style={{
                               width : '100%',
                           }}
                    InputLabelProps={{
                        classes: {
                            root: classes.cssLabel,
                            focused: classes.cssFocused,
                        },
                    }}
                    InputProps={{
                        classes: {
                            root: classes.cssOutlinedInput,
                            focused: classes.cssFocused,
                            notchedOutline: classes.notchedOutline,
                        },
                    }}
                    label="Enter 10 digit mobile number"
                    variant="outlined"
                    id="custom-css-outlined-input"
                    value={this.state.phone || ''}
                    onChange={this.handlePhoneChange}
                />


                {/**----------------**/}
            </Grid>

            <Grid item xs={12} style={{marginTop:'33px'}}>
                <Button fullWidth={true} variant="text" style={{
                    backgroundColor:'green',
                    color:'white'
                }} onClick={()=>this.toggleRequestOtp(true)}>
                    REQUEST OTP
                </Button>
            </Grid>

            <Grid item xs={12} style={{marginTop:'30%'}}>
                <Typography inline={true} align="center" variant="subtitle1" style={{
                    fontColor:'grey',
                    display:'block',
                }}>
                    Or Sign in With
                </Typography>
            </Grid>
        </Grid>
          <Grid container={true} spacing={0}  style={{marginTop:'30px',paddingLeft:'16px',paddingRight:'16px'}}>
              <Grid item xs={4} style={{textAlign:'center'}}><IconButton><FacebookIcon/></IconButton></Grid>
                    <Grid item xs={4} style={{textAlign:'center'}}><IconButton><GoogleIcon/></IconButton></Grid>
                    <Grid item xs={4} style={{textAlign:'center'}}><IconButton onClick={()=>this.changeEmailState(true)}><EmailRounded style={{fontSize:45}}/></IconButton></Grid>
          </Grid>
          <Snackbar open={snackbarOpen} autoHideDuration={3000} onClose={this.handleClose}>
              <MySnackbarContentWrapper
                  onClose={this.handleClose}
                  variant={messageType}
                  message={messages[messageType]}
              />
          </Snackbar>
      </div>);
  }
}

LoginPage.propTypes = {
    classes:PropTypes.object.isRequired
};

function mapStateToProps(state) {
    return {
        ...state
    }
}
function mapDispatchToProps(dispatch){
    return bindActionCreators({

    },dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(LoginPage));