const actions = {
    TOGGLE_SIDEBAR:'TOGGLE_SIDEBAR',
    UPDATE_RESTAURANT_DATA:'UPDATE_RESTAURANT_DATA',
    INJECT_RESTAURANT:'INJECT_RESTAURANT',
    UPDATE_REVIEWS : 'UPDATE_REVIEWS',
    FORCED_EMPTY_REVIEWS : 'FORCED_EMPTY_REVIEWS',
    UPDATE_DISHES : 'UPDATE_DISHES',
    FORCED_EMPTY_DISHES : 'FORCED_EMPTY_DISHES',


    FETCH_DISHES:'FETCH_DISHES',
    FETCH_DISHES_SUCCESS:'FETCH_DISHES_SUCCESS',
    FETCH_DISHES_FAILURE:'FETCH_DISHES_FAILURE',
    FETCH_REVIEWS : 'FETCH_REVIEWS',
    FETCH_REVIEWS_SUCCESS : 'FETCH_REVIEWS_SUCCESS',
    FETCH_REVIEWS_FAILURE : 'FETCH_REVIEWS_FAILURE',

    ADD_FAKE_REVIEW_SKELETON : 'ADD_FAKE_REVIEW_SKELETON',
    ADD_FAKE_DISHES_SKELETON : 'ADD_FAKE_DISHES_SKELETON',


    toggleSidebar :(val)=>{
        return {
            type : actions.TOGGLE_SIDEBAR,
            payload : val
        }
    },

    updateRestaurantData:(val)=>{
      return{
          type:actions.UPDATE_RESTAURANT_DATA,
          payload:val
      }
    },

    updateRestaurantByIdData:(val)=>{
        return{
            type:actions.INJECT_RESTAURANT,
            payload:val
        }
    },
    fetchDishes:(start,limit)=>{
        console.log("start = ",start," limit = ",limit);
        return {
            type : actions.FETCH_DISHES,
            payload : {start,limit}
        }
    },

    fetchReviews:(start,limit)=>{
        console.log("chala kya");
        return {
            type : actions.FETCH_REVIEWS,
            payload : {start,limit}
        }
    },
    addFakeReviewSkeletons:()=>{
      return {
          type : actions.ADD_FAKE_REVIEW_SKELETON,
      }
    },
    addFakeDishSkeletons:()=>{
      return {
          type : actions.ADD_FAKE_DISHES_SKELETON,
      }
    },


};

export default actions;