import actions from '../actions/index';

const initState = {openSidebar:false,dishData:[{},{},{},{},{},{}],start : 0};

export default function restaurantReducer(state=initState,action){
    let {dishData} = state;
  switch (action.type) {
      case actions.FETCH_DISHES_SUCCESS :
          if(checkdishDataIsAtInitialState(dishData))
          {
                return {
                    ...state,
                    dishData : action.payload,
                    start : action.payload.length,
                }
          }
          else
          {
              dishData.splice(dishData.length-2,2);
              let ans = dishData.concat(action.payload);
              return {
                  ...state,
                  dishData : ans,
                  start : ans.length
              }
          }
      case actions.FETCH_DISHES_FAILURE :
          return {
              ...state,
          };
      case actions.ADD_FAKE_DISHES_SKELETON :
          return {
              ...state,
                dishData : dishData.concat([{},{}])
          };
      default : return{
          ...state
      }
  }  
};

function checkdishDataIsAtInitialState(dishData) {
    if(dishData.length !== 6)
        return false;
    for(let i=0;i<dishData.length;i++)
    {
        if(!Object.entries(dishData[i]).length === 0 || !dishData[i].constructor === Object)
            return false;
    }
    return true;
}