import React from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {withStyles} from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import {Grid, IconButton, TextField,Button} from '@material-ui/core';
import {Link} from "react-router-dom";
import {ArrowBack} from "@material-ui/icons";

const styles = theme=>({
    parent:{
        width:'100vw',
        height:'100vh',
    },
    sendEmailButton:{
        width:'50%',
        color:'white',
        backgroundColor:'#006400'
    }
});


class ForgotPassword extends React.Component{
    render()
    {
        const {classes} = this.props;
        return(
            <div className={classes.parent}>
                <Grid container={true} spacing={0} style={{paddingLeft:'16px',paddingRight:'16px'}}>
                    <Grid item xs={12} style={{marginTop:'7%'}}>
                        <Link to="/"><IconButton style={{paddingLeft:'0px'}}>
                            <ArrowBack/>
                        </IconButton></Link>
                    </Grid>
                </Grid>
                <Grid container={true} spacing={0} style={{paddingLeft:'32px',paddingRight:'32px'}}>
                    <Grid item xs={12} style={{marginTop:'14%'}}>
                        <TextField
                            id="email"
                            label="Enter your Email ID"
                            style={{
                                width:'100%'
                            }}
                            name="email"
                            value={this.state.email}
                        />
                    </Grid>
                    <Grid item xs={12} style={{marginTop:'20%',textAlign:'center'}}>
                        <Button variant="contained" className={classes.sendEmailButton} >
                            Send Email
                        </Button>
                    </Grid>
                </Grid>
            </div>
        )
    }
}

ForgotPassword.propTypes = {
    classes:PropTypes.object.isRequired
};

function mapStateToProps(state)
{
    return {
        ...state
    }
}
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({},dispatch);
}

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(ForgotPassword));