import React from 'react';
import {withStyles} from '@material-ui/core/styles'
import {Card,
    CardActions,
    CardContent,
    CardHeader,
    Typography,
    IconButton} from '@material-ui/core';
import {ThumbUpAlt,ThumbDownAlt,Star,StarBorder} from '@material-ui/icons';
import PropTypes from 'prop-types';
import moment from 'moment';
import Skeleton from 'react-loading-skeleton';

const styles = (theme)=>({
    root : {
        maxWidth : '100%',
    },
    avatar : {
        borderRadius :'50%',
        maxWidth : '100%',
        height : 'auto',
        marginTop : '15%'
    },
    avatarContainer : {
        width:'12.5%'
    },
    cardContainer : {
        width:'87.5%'
    },
    container : {
      display : 'flex',
        maxWidth : '100%',
        // boxShadow: '0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12)',
        // borderRadius: '4px',
        // backgroundColor:'#fff',
        padding : '2%'
    },
    card : {
        boxShadow:'none'
    },
    cardHeader : {
      padding : '2%',
        maxWidth : '100%',
        paddingLeft : '4%',
        paddingRight : '2%'
    },
    cardContent : {
      padding :'2%',
        maxWidth : '100%',
        paddingRight : '2%',
        paddingLeft : '4%'
    },
    actions : {
        display : 'flex',
        padding : 0
    },
    expandedReview : {
        fontSize : '0.84rem',
        fontFamily : 'sans-serif',
        lineHeight : '16px'
    },
    shrunkReview : {
        textOverflow:'ellipsis',
        overflow: 'hidden',
        display:'-webkit-box',
        lineHeight:'16px',   /* fallback */
        maxHeight:'48px',    /* fallback */
        // '-webkit-line-clamp': 2 /* number of lines to show */,
        WebkitLineClamp:3,
        WebkitBoxOrient:'vertical',
        fontSize : '0.84rem',
        fontFamily : 'sans-serif'
        // -webkit-box-orient: vertical
    },
});

class ReviewComponent extends React.Component{

    state = {
        liked : false,
        disliked : false,
        readExpandedVersion : false,
    };

    clickExpand=(val)=>{
      this.setState({readExpandedVersion : val});
    };

    createStars=(stars)=>{
        let children = [];
        for(let i=1;i<=5;i++)
        {
            children.push(
                i<=stars ? <Star style={{fill : '#f7c41b' , fontSize : '1.1rem'}} key={i}/> : <StarBorder style={{fill : '#f7c41b',fontSize:'1.1rem'}} key={i}/>
            )
        }
        return children;
    };

    likeClicked = ()=>{
      let {liked,likeCount,disliked,dislikeCount} = this.state;
      likeCount = likeCount ? likeCount : this.props.likes;
      dislikeCount = dislikeCount ? dislikeCount : this.props.disLikes;
      if(!likeCount || !dislikeCount)
          return;
      if(liked)
          likeCount-=1;
      else{
          if(disliked)
          {
              disliked = false;
              dislikeCount-=1;
          }
          likeCount+=1;
      }

      this.setState({liked : !liked,likeCount : likeCount,disliked:disliked,dislikeCount : dislikeCount});
      /**do api request to update the like and dislike count here**/
    };

    dislikeClicked = ()=>{
        let {liked,likeCount,disliked,dislikeCount} = this.state;
        likeCount = likeCount ? likeCount : this.props.likes;
        dislikeCount = dislikeCount ? dislikeCount : this.props.disLikes;
        if(!likeCount || !dislikeCount)
            return;
        if(disliked)
            dislikeCount-=1;
        else
        {
            if(liked)
            {
                liked = false;
                likeCount-=1;
            }
            dislikeCount+=1;
        }

        this.setState({disliked : !disliked,dislikeCount : dislikeCount,liked : liked,likeCount:likeCount});
        /** do api request here to update the like and dislike count (in background) */
    };


    render()
    {
        const {classes} = this.props;
        // const {name , avatar , timeOfCreation, keyLine, content , stars, likeCount, dislikeCount} = this.state;
        const {name,avatar,timeOfCreation,keyLine,content,stars,likes,disLikes} = this.props;
        return(
          <div className={classes.root}>
               <div className={classes.container}>
                   <div className={classes.avatarContainer}>
                       {
                           (name===null || name===undefined)&&(
                               <Skeleton circle={true} width="100%" height="21%"/>
                           )
                       }
                       {
                           (name)&&(
                               <img src={avatar ? avatar : "http://minervastrategies.com/wp-content/uploads/2016/03/default-avatar.jpg"} className={classes.avatar} alt="avatar"/>
                           )
                       }

                   </div>
                   <div className={classes.cardContainer}>
                       <Card className={classes.card}>
                           <CardHeader
                           title = {(name&&stars) ? (
                               <span style={{display : 'flex'}} >
                                    <Typography inline={true} style={{fontSize : '0.99rem'}}>
                                        {name}
                                    </Typography>
                                    <Typography  style={{marginLeft :'auto',fontSize:'0.95rem'}}>
                                        {this.createStars(stars)}
                                    </Typography>
                                </span>
                           ) : (
                               <Skeleton width="100%" height="100%"/>
                           )}
                           disableTypography={true}
                           subheader={(timeOfCreation ? (
                               <Typography variant="caption" style={{fontSize :'0.7rem',WebkitFontSmoothing :'subpixel-antialiased'}}>{moment(timeOfCreation).fromNow()}</Typography>
                           ) : (
                               <Skeleton height="100%" width="25%"/>
                           ))} className={classes.cardHeader}/>
                           <CardContent className={classes.cardContent}>
                               {
                                   (keyLine&&content)&&(<div>
                                       <Typography style={{
                                           fontSize : '0.85rem',
                                           fontWeight : 'bold'
                                       }} gutterBottom={true}>
                                           "{keyLine}"
                                       </Typography>
                                       <Typography component="p" className={this.state.readExpandedVersion ? classes.expandedReview : classes.shrunkReview}>
                               {content}
                                   </Typography>
                                   <span style={{fontSize : '0.75rem', color : 'royalblue'}} onClick={()=>this.clickExpand(!this.state.readExpandedVersion)}>{this.state.readExpandedVersion ? "read less" : "read more"}</span>
                                       </div>
                                   )
                               }
                               {
                                   (!keyLine&&!content)&&(
                                       <Skeleton width="100%" count={5}/>
                                   )
                               }
                           </CardContent>
                           <CardActions className={classes.actions}>
                               <IconButton onClick={()=>this.likeClicked()}><ThumbUpAlt style={{fill:this.state.liked ? 'cornflowerblue' : "LightGrey"}}/>
                                   <Typography variant="caption" style={{marginLeft : '10%',marginTop : '10%'}}>{this.state.likeCount || likes}</Typography></IconButton>
                               <IconButton onClick={()=>this.dislikeClicked()}>
                                   <ThumbDownAlt style={{fill : this.state.disliked ? 'cornflowerblue' : 'LightGrey'}}/>
                                   <Typography variant="caption" style={{marginLeft : '10%',marginTop : '10%'}}>{this.state.dislikeCount || disLikes}</Typography>
                               </IconButton>
                           </CardActions>
                       </Card>
                   </div>
               </div>
          </div>
        );
    }
};

ReviewComponent.propTypes={
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ReviewComponent);