import actions from '../actions/index';

const initState = {openSidebar:false,restaurantData:[]};
export default function restaurantReducer(state=initState,action){
    switch(action.type){
        case actions.UPDATE_RESTAURANT_DATA:
            let {restaurantData} = state;
            return{
                ...state,
                restaurantData:restaurantData.concat(action.payload)
            };
        case actions.INJECT_RESTAURANT:{
            return{
                ...state,
                injectedRestaurant:action.payload
            }
        }
        default : return{
            ...state
        };
    }
};