import React, { Component } from 'react';
import './App.css';
// import {Provider} from 'react-redux';
// import {createStore,applyMiddleware} from 'redux';
import {connect} from 'react-redux';
import {Route,BrowserRouter} from 'react-router-dom';
import {bindActionCreators} from 'redux';
import Sidebar from './containers/sidebar';
import MainPage from './containers/MainPage';
import LoginPage from './containers/login';
import SignUpPage from './containers/signup';
import ForgotPassword from './containers/forgotPassword';
import Restaurant from './containers/restaurant';
import Review from './containers/review';

class App extends Component {
    // componentDidMount()
    // {
    //     const script = document.createElement("script");
    //     script.src = "https://maps.googleapis.com/maps/api/js?key=AIzaSyCJdS5A0xf5rXe4CQqvUZRCFpjQ28CIT8k&callback=initMap";
    //     script.async = true;
    //     document.body.appendChild(script);
    // }
  render() {
      return (
              <BrowserRouter>
                  <div className="App">
                      <Route path="/" exact component={MainPage}/>
                      <Route path="/login" exact component={LoginPage}/>
                      <Route path="/forgotPassword" exact component={ForgotPassword}/>
                      <Route path="/signup" exact component={SignUpPage}/>
                      <Route path="/restaurant/:restaurant_name" exact component={Restaurant}/>
                      <Route path="/restaurant/:restaurant_name/review" exact component={Review}/>
                      <Sidebar/>
                  </div>
              </BrowserRouter>
          );
  }
}

function mapStateToProps(state)
{
  return {
      ...state
  }
}
function mapDispatchToProps(dispatch)
{
  return bindActionCreators({},dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(App);
